@import url(https://fonts.googleapis.com/css?family=Open+Sans|Sen&display=swap);
:root{--title-font: 'Sen', serif;--body-font: 'Open Sans', sans-serif;--color-main: yellowgreen;--color-main-dark: green;--color-accent: rgb(74, 193, 233);--color-accent-dark: rgb(45, 159, 197);--color-gray: gray;font-family:'Open Sans', sans-serif;font-family:var(--body-font)}h1,h2,h3,h4,h5,h6{font-family:'Sen', serif;font-family:var(--title-font)}a{color:rgb(74, 193, 233);color:var(--color-accent);text-decoration:none}a:hover{text-decoration:underline}main{max-width:800px;width:90vw;margin:auto}header,footer{display:flex;flex-flow:row wrap;justify-content:center;align-items:center;margin:auto;font-family:'Sen', serif;font-family:var(--title-font);color:gray;color:var(--color-gray)}header>a:hover{text-decoration:none}header .logo{font-size:15pt}header nav{margin:2px 8px}header nav *{text-decoration:none;color:inherit;margin:0 3px}footer{font-size:10pt}footer .logo{display:inline}img{width:300px;max-height:500px;float:left;margin-right:20px}

.logo{font-family:var(--title-font);text-transform:none;text-decoration:none;font-weight:bold}.logo .solar{color:var(--color-main-dark)}.logo .food{color:var(--color-main)}

